import { React, useState } from "react";
import MusicListData2 from "../music2.json";
import MusicListData3 from "../music3.json";
import Player from "./Player";
import "react-h5-audio-player/lib/styles.css";

import { Link } from "react-router-dom";

function Playlist() {
  const musicList2 = MusicListData2;
  const musicList3 = MusicListData3;

  const [id, setId] = useState("");
  const [coverimage, setCoverimage] = useState("");
  const [title, setTitle] = useState("");
  const [artist, setArtist] = useState("");
  const [music, setMusic] = useState("");
  const [open, setOpen] = useState(false);

  function handleOpen(id, coverimage, title, artist, music) {
    setId(id);
    setCoverimage(coverimage);
    setTitle(title);
    setArtist(artist);
    setMusic(music);
    setOpen(true);
  }

  function handleClose() {
    setId("");
    setCoverimage("");
    setTitle("");
    setArtist("");
    setOpen(false);
  }

  return (
    <div className="playlist">
      <div>
        <h2>STR</h2>
      </div>
      <div className="playlist3">
        {musicList3.slice(0, 6).map((playlist) => {
          return (
            <Link to={`/Playlist/${playlist.id}`}>
              <div className="tune" key={playlist.id}>
                <img src={`${playlist.coverimage}`} alt="" />
                <p className="tuneTitle">{playlist.title}</p>
                <p className="tuneCaption">{playlist.caption}</p>
              </div>
            </Link>
          );
        })}
      </div>
      <div>
        <h2></h2>
      </div>
      <div className="playlist3">
        {musicList3.slice(6, 12).map((playlist) => {
          return (
            <Link to={`/Playlist/${playlist.id}`}>
              <div className="tune" key={playlist.id}>
                <img src={`${playlist.coverimage}`} alt="" />
                <p className="tuneTitle">{playlist.title}</p>
                <p className="tuneArtist">{playlist.caption}</p>
              </div>
            </Link>
          );
        })}
      </div>
      <div>
        <h2>New Generated Artists</h2>
      </div>
      <div className="playlist3">
        {musicList3.slice(12, 18).map((playlist) => {
          return (
            <Link to={`/Playlist/${playlist.id}`}>
              <div className="tune" key={playlist.id}>
                <img src={`${playlist.coverimage}`} alt="" />
                <p className="tuneTitle">{playlist.title}</p>
                <p className="tuneArtist">{playlist.caption}</p>
              </div>
            </Link>
          );
        })}
      </div>
    </div>
  );
}

export default Playlist;
